import React, {useState, useEffect} from 'react'
import './utils/NavBar.css'
import './utils/NavBarV2.css'
import {Link} from 'react-router-dom';


function NavBarV2({history}) {
    const [scrollPosition, setScrollPosition] = useState(0);
    const [back, setBack] = useState(0);

    const updateScroll = () => {
        setScrollPosition(window.scrollY || document.documentElement.scrollTop);
    }
    useEffect(()=>{
        window.addEventListener('scroll', updateScroll);
        if(scrollPosition < 50){
            setBack(scrollPosition*(1/50));
        }else{
            setBack(1);
        }
    });

    return (
        <div className={scrollPosition < 50 ? "nav-bar-container-top" : "nav-bar-container"} 
        style={{backgroundColor:`rgba(255,255,255,1)`, color:`rgba(${255-back*149},${255-back*156},${255-back*8},1)`
        }}>
            <div style={{width:'100%', paddingLeft:'12vw'}}>
                <Link to="/v2" className="nav-bar-title" style={{cursor:'pointer'}}>
                    Surfee
                </Link>
                <Link to="/info" className="nav-left-text" style={{marginLeft:'4vw'}}>
                    제품소개
                </Link>
                <Link to="/questions" className="nav-left-text">
                    문의하기
                </Link>
            </div>
            <span className="nav-on">
                <button className="nav-button-round" onClick={() => window.scrollTo(0,document.body.scrollHeight)}>무료로 체험하기</button>
            </span>
        </div>
    )
}

export default NavBarV2
