import React, {useState, useEffect} from 'react';
import './utils/ResponsePage.css';
import {dbService} from '../tools/fbase';

 // Replace with your client ID from the developer console.
const CLIENT_ID = "335393250887-988oqrccovm5stnfn1n2l08fmg65c940.apps.googleusercontent.com"; // <-- 발급받은 Client ID 입력 

 // Set authorized scope.
const SCOPES = ['https://www.googleapis.com/auth/analytics.readonly'];

function ResponsePage() {
    const [id, setId] = useState("");
    const [loading, setLoading] = useState(false);
    const [reponses, setReponses] = useState([{
        email:"",
        created:0
    }]);
    const [page, setPage] = useState(1);
    const [pages, setPages] = useState([]);
    
    useEffect(() => {
        getDatas();
    },[loading])

    const getDatas = async () => {
        const reDatas = await dbService
        .collection("apply-datas")
        .orderBy("created", "desc")
        .get(); // uid를 creatorId로 줬었으니까.

        let reData = reDatas.docs.map(doc => {
            return({...doc.data(), id:doc.id})
        });
        setReponses(reData);
        let p = [];
        for(var i = 0; i<Math.ceil(reData.length/10); i++){
            p.push(i+1);
        }
        setPages(p);
        setLoading(true);
    }

    const ResponseTable = () => {
        
        let content = reponses.slice((page-1)*10+1,(page)*10+1);

        return(
            content.map((item, index) => {
                // 지금 1이면? => 1~10
                // 지금 2면? => 11 ~ 20
                let day = new Date(item.created)

                const numth = (page - 1) * 10 + index + 1;
        
                return (
                    <span index={index} style={{display:'flex', flexDirection:'row', marginTop:'2px'}} className="response-table-one-content">
                        <span style={{fontWeight:'700', width:'20vw'}}>{numth}- {item.email}</span>
                        <span> 2021년 {day.getMonth()+1}월 {day.getDate()}일 {day.getHours()}시에 신청 </span>
                    </span>
                )
            })
        )
    }

    const ResponseTabs = pages.map((item, index) => {
        if(item === page){
            return(
                <span index={index} style={{backgroundColor:'red'}} onClick={e => setPage(item)} className="one-pagination">{item}</span>
            )
        }
        return(
            <span index={index} onClick={e => setPage(item)} className="one-pagination">{item}</span>
        )
    })

    if(loading === true){
        return (
            <div className="response-container">
                <div className="response-table">
                    <div className="response-table-top">
                        <span style={{fontSize:'30px'}}> 신청한 사람 수 : 총 {reponses.length} 명 </span>
                        <span style={{fontSize:'30px'}}> 신청자 이메일 , 신청 날짜 </span>
                    </div>
                    <div style={{display:'flex', flexDirection:'column', marginTop:'5%', textAlign:'left'}}>
                        {ResponseTable()}
                    </div>
                    <div style={{display:'flex', flexDirection:'row'}}>
                        {ResponseTabs}
                    </div>
                </div>
            </div>
        )
    }else{
        return(
            <></>
        )
    }
}

export default ResponsePage
